<template>
  <header>
      <button @click="onClick" class="btn transparent" id="btn"><i class="fas fa-bars"></i></button>
      <button @click="logOut" class="btn">LogOut</button>
  </header>
</template>

<script>
export default {
    name:"Header",
    methods:{
        onClick(){
            document.getElementById("btn").classList.add("active");
            setTimeout(()=>{
                document.getElementById("btn").classList.remove("active");
            },400)
            this.$store.commit("ShowOrHide")
        },
        logOut(){
            window.localStorage.removeItem("token");
            this.$router.replace("/panel/admin/");
        }
    }
}
</script>

<style>
header{
    background-color:#114358;
    padding:8px 5px;
    box-shadow:0 0 2px 4px rgba(255,255,255,.4);
    display:flex;
    justify-content:space-between;
}
.btn{
    position:relative;
    background-color:#f2aa1f;
    appearance: none;
    border:none;
    color:#fff;
    padding:10px;
    display:flex;
    font-size:20px;
    font-weight: 600;
    cursor:pointer;
    border-radius: 8px;
    justify-content:center;
    align-items:center;
    transition:all .2s;
}

.btn:hover{
    background-color:#ffc34b;
}

.btn:after{
    position:absolute;
    content:"";
    background-color:rgba(255,255,255);
    top:50%;
    transform:translate(-50%,-50%);
    left:50%;
    transition:all .1s;
    border-radius:50%;
}

.btn.active::after{
    animation:tap .4s;
}

.btn.transparent{
    background-color:unset;
}

@keyframes tap{
    0%{
        height:0;
        width:0;
        opacity:.5;
    }
    100%{
        height:100%;
        width:100%;
        opacity:0;
    }
}

</style>