<template>
  <div class="lds-dual-ring"></div>
</template>

<script>
export default {
    name:"Spinner"
}
</script>

<style>
.lds-dual-ring {
  display: flex;
  justify-content:center;
  align-items:center;
  width: 100%;
  height: 100%;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #114358;
  border-color: #114358 transparent #114358 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>