<template>
  <Container>
    <Spinner v-if="!loaded"/>
    <div class="users-view" v-else>
      <div class="page-selected">
        <h3>Pagina: </h3>
        <div class="select">
          <select @change="handleChange" v-bind:value="page">
            <option v-for="page in totalPages" v-bind:key="page" v-bind:value="page+1">{{page+1}}</option>
          </select>
        </div>
      </div>
      <div class="users">
        <div class="user head">
          <h2>ID</h2>
          <h2>Username</h2>
          <h2>Correo</h2>
          <h2>Tips Publicos</h2>
          <h2>Helps publicos</h2>
          <h2>Problems Publicos</h2>
        </div>
        <div class="user" v-for="user in users" v-bind:key="user">
          <span>{{user.id}}</span>
          <span>{{user.userName}}</span>
          <span>{{user.email}}</span>
          <span>{{user.publicTips}}</span>
          <span>{{user.publicHelps}}</span>
          <span>{{user.publicProblems}}</span>
        </div>
      </div>
    </div>
  </Container> 
</template>

<script>
import axios from 'axios';
import Container from './Container.vue'
import Spinner from './Spinner.vue'
export default {
  name:"Users",
  components:{
    Container,
    Spinner
  },
  data(){
    return{
      users: [],
      page:1,
      loaded:false,
      totalPages:null
    }
  },
  mounted(){
    this.getAllUsers();
  },
  methods:{
    async getAllUsers(){
      this.loaded = false;
      const res = await axios.get(`https://identity-web-service.herokuapp.com/api/users?page=${this.page}`,{
        headers:{
          "Authorization":"Bearer "+window.localStorage.getItem("token")
        }
      });
      const data = res.data.content;
      this.users = data.items;
      this.totalPages = Array.from(Array(data.pages).keys());
      this.loaded = true;
    },
    handleChange(e){
      this.page = (e.target.value);
    }
  },
  watch:{
    page:function (){
      this.getAllUsers();
    }
  }
}
</script>

<style scoped>
.page-selected{
  display:flex;
  align-items:center;
  margin-bottom:10px;
}
.select {
  width: 100%;
  min-width: 3ch;
  max-width: 5ch;
  border: 1px solid var(--select-border);
  border-radius: 0.25em;
  font-size: 1.25rem;
  cursor: pointer;
  line-height: 1.1;
  background-color: #fff;
  align-items:center;
  display:grid;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
  grid-template-areas: "select";
}
select,
.select:after {
  grid-area: select;
}
.select::after {
  content: "";
  justify-self:end;
  width: 0.8em;
  height: 0.5em;
  background-color: var(--color);
  clip-path: polygon(100% 0%, 0 0%, 50% 100%);
}
select {
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0.25em 0.5em;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  outline:none;
  line-height: inherit;
}
.users{
  display:grid;
  grid-template-columns: repeat(6,1fr);
}

.user{
  display:contents;
}

.user > span{
  padding:8px 4px;
  word-wrap: break-word;
  background:#fff;
  border:1px solid black;
  font-weight:500;
}

.user.head > h2{
  background-color:#114358;
  font-size:clamp(18px,2.5vw,24px);
  display:flex;
  justify-content:center;
  align-items: center;
  text-align:center;
  color:#fff;
}

</style>