<template> 
<div class="container">
    <h1>
        NoName Administration Panel
    </h1>
    <form class="form-login" @submit.prevent="onSubmit">
        <div class="form-group">
            <input type="email" v-model="gmail" class="form-control" placeholder="E-mail: "/>
            <div class="bar" />
        </div>
        <div class="form-group">
            <input type="password" v-model="password" class="form-control" placeholder="Password: "/>
            <div class="bar" />
        </div>
        <div class="form-group">
            <input type="submit" class="btn" value="Login"/>
        </div>
    </form>
</div>
</template>

<script>
import axios from 'axios';
import jwt from 'jwt-decode';
export default {
    name:"Login",
    data(){
        return{
            gmail:"",
            password:""
        }
    },
    methods:{
        async onSubmit(){
            const res = await axios.post("https://identity-web-service.herokuapp.com/api/users/login",JSON.stringify({
            username: "",
            password: this.password,
            email: this.gmail,
            }),{
                headers:{
                    "Content-Type": "application/json"
                }
            });
            const data = await res.data.content;
            const userData = jwt(data.token,process.env.TOKEN);
            console.log(userData)
            if(userData["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"] === "Administrator"){
                window.localStorage.setItem("token",data.token);
                window.localStorage.setItem("expiration",data.expiration);
                this.$router.replace("/panel/admin/home");
            }else{
                alert("No tienes permiso");
            }
        }
    }
}
</script>
<style>
.bar{
    position: relative;  
    display: block; 
    width: 100%; 
}
.bar:before,.bar:after{
    content: '';
    height: 2px; 
    width: 0;
    bottom: 1px; 
    position: absolute;
    background: #114358; 
    transition: 0.2s ease all; 
    -moz-transition: 0.2s ease all; 
    -webkit-transition: 0.2s ease all;
}

.bar:before{
    left:50%;
}

.bar:after{
    right: 50%;
}

.form-control:focus ~ .bar:before,.form-control:focus ~ .bar:after{
    width:50%;
}
</style>
<style scoped>
.container{
    align-items: center;
    display:flex;
    background-color:#ecf0f1;
    flex-direction: column;
    background-image: url("data:image/svg+xml,%3Csvg width='84' height='48' viewBox='0 0 84 48' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0h12v6H0V0zm28 8h12v6H28V8zm14-8h12v6H42V0zm14 0h12v6H56V0zm0 8h12v6H56V8zM42 8h12v6H42V8zm0 16h12v6H42v-6zm14-8h12v6H56v-6zm14 0h12v6H70v-6zm0-16h12v6H70V0zM28 32h12v6H28v-6zM14 16h12v6H14v-6zM0 24h12v6H0v-6zm0 8h12v6H0v-6zm14 0h12v6H14v-6zm14 8h12v6H28v-6zm-14 0h12v6H14v-6zm28 0h12v6H42v-6zm14-8h12v6H56v-6zm0-8h12v6H56v-6zm14 8h12v6H70v-6zm0 8h12v6H70v-6zM14 24h12v6H14v-6zm14-8h12v6H28v-6zM14 8h12v6H14V8zM0 8h12v6H0V8z' fill='%23ffffff' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E");
}
.form-login{
    width: 40vw;
    height:50%;
    display:flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
    background-color:#fff;
    border-radius:8px;
}

h1{
    margin-top:clamp(100px,5vw,200px);
    font-size:clamp(45px,5vw,62px);
    margin-bottom:20px;
    width:clamp(200px,70vw,800px);
    font-weight:600;
    text-align:center;
}

.form-group{
    padding:10px 5px;
}

.form-control{
    appearance: none;
    background-color:rgba(0,0,0,.05);
    border:none;
    padding:4px 8px;
    font-size:clamp(22px,2.5vw,26px);
