<template>
  <Container>
    <Spinner v-if="!loaded"/>
    <div class="home-view" v-else>
      <div class="card-wrapper">
          <Card title="Total de Usuarios:" v-bind:body="userTotal"/>
          <Card title="Total de Problemas: " v-bind:body="problemTotal" />
          <Card title="Total de Ayudas: " v-bind:body="helpTotal" />
      </div>
    </div>
  </Container>
</template>

<script>
import axios from 'axios';
import Container from './Container'
import Card from './Card'
import Spinner from './Spinner'
export default {
    name:"Home",
    components:{
      Container,
      Card,
      Spinner
    },
    data(){
      return{
        loaded:false,
        userTotal:0,
        problemTotal:0,
        helpTotal:0
      }
    },
    mounted(){
      this.getAllData()
    },
    methods:{
      async getAllData(){
        await this.getUserData()
        await this.getProblemsData()
        await this.getHelpData()
        this.loaded = true;
      },
      async getUserData(){
        const res = await axios.get(`https://identity-web-service.herokuapp.com/api/users`,{
        headers:{
          "Authorization":"Bearer "+window.localStorage.getItem("token")
        }
        });
        const data = res.data.content;
        this.userTotal = data.total;
      },
      async getProblemsData(){
        const res = await axios.get("https://amigonimo-web-api.herokuapp.com/api/problems");
        const data = res.data.content;
        this.problemTotal = data.total;
      },
      async getHelpData(){
        const res = await axios.get("https://amigonimo-web-api.herokuapp.com/api/helps");
        const data = res.data.content;
        this.helpTotal = data.total;
      }
    }
}
</script>

<style scoped>
.card-wrapper{
  display:flex;
  flex-wrap: wrap;
  justify-content:space-between;
}

.card{
  margin:10px;
}
</style>